<template>
  <div class="content">
    <p class="fhbtn"><a href="mydoc_m.html"></a></p>
    <h1>
      <input type="file" class="file" ref="f1">
      <p>上传头像</p>
    </h1>
    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
        <router-link to="/login">登录</router-link>
        <span></span>
        <router-link to="/reg">注册</router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
      <li class="lifirst">
        <input type="text" value="" v-model="username"/>
        <span>帐号</span>
      </li>
      <li>
        <input type="text" value="" v-model="password"/>
        <span>密码</span>
      </li>
    </ul>
    <div class="footbox">
      <p>{{miss}}</p>
      <input type="button" value="注 册" class="login-btn" @click="reg"/>
      <a href="javascript:;" class="tishi">忘记密码？</a>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      miss:''
     }
  },
  methods:{
    reg(){
      let formData = new FormData();
      formData.append('username',this.username)
      formData.append('password',this.password)
      formData.append('icon',this.$refs.f1.files[0])
      formData.append('nikename',this.username)
      axios({
        url:'/api/reg',
        method:'post',
        // data:'string/obj/urlSearchParams/formData'
        data:formData
      }).then(
        res => res.data.err===0 ? this.$router.push('/login') : this.miss=res.data.msg
      )
    }
  }
}
</script>


