import Vue from "vue";
import axios from "axios";
import store from './store'

     // 添加一个请求的拦截器;config={header,params,data,method}
    axios.interceptors.request.use(function(config){
        store.commit('VIEW_LOAD',true)
        // console.log('拦截器 go',vm.$data.bLoad)
        return config;
    },function(error){
        return Promise.reject(error);
    });
    // 添加一个响应的拦截器
    axios.interceptors.response.use(function(response){
        store.commit('VIEW_LOAD',false)
        // console.log('拦截器 back')
        return response;
},function(error){
        return Promise.reject(error);
})
 

Vue.prototype.$axios=axios;
window.axios = axios;

export default axios;