<template>
  <div class="home">
    <Slider :banner="banner" dataName="banner"></Slider>
    <List :list="list" dataName="home"></List>
  </div>
</template>
<script>
// import Slider from '../components/Slider'
import Slider from '@/components/Slider'
import List from '@/components/List'


export default {
  name:'home',
  data(){
    return {
      list:[],
      banner:[]
    }
  },
  components:{Slider,List},
  activated(){
    axios({
      url:'/api/home'
    }).then(
      res=>this.list=res.data.data
    ),
    axios({
      url:'/api/banner'
    }).then(
      res=>this.banner=res.data.data
    )
  }
}
</script>

