import axios from "../plugins/axios";
import * as type from './type'

export default {

    ['VIEW_NAV']: ({ commit, state }, payload) => commit('VIEW_NAV', payload),
    [type.VIEW_FOOT]: ({ commit, state }, payload) => commit('VIEW_FOOT', payload),
    [type.VIEW_LOAD]: ({ commit, state }, payload) => commit('VIEW_LOAD', payload),

    [type.UPDATE_USER]: ({ commit, state }, payload) => {
        let res = axios({ url: '/api/user' })
        commit('UPDATE_USER', res.data)
    },
    [type.UPDATE_DETAIL]: ({ commit, state }, { type, payload }) => {
        // console.log('UPDATE_DETAIL',payload)
        axios({
            url: `api/${payload.dataName}/${payload.id}`
        }).then(
            res => {
                commit('UPDATE_DETAIL', res.data.data)
            }
        )
    },
    addUser: ({ commit, state }, payload) => {
        let arr = [...state.like];
        let onoff = 0;
        if (arr.length == 0) {
            arr.push(payload)
        } else {
            arr.forEach((item, index) => {
                if (item.id == payload.id) {
                    onoff = 1
                }
            })
            if (!onoff) {
                arr.push(payload)
            }
        }

        commit(type.ADD_USER, arr)

    },
    delUser: ({ commit, state }, payload) => {
        state.like.forEach((item, index) => {
            if (item.id == payload.id) {
                state.like.splice(index, 1);
            }
        });
        commit(type.DEL_USER, [...state.like])
    },
};