<template>
  <div id="app">
    <Loading v-show="bLoad"></Loading>
    <Header v-show="$store.state.bNav"></Header>
    <keep-alive
      :exclude="['Detail']"
    >
    <router-view></router-view>
    </keep-alive>
    <Footer v-show="bFoot"></Footer>
  </div>
</template>

<script>
import Follow from './pages/Follow'
import Home from './pages/Home'
import Error from './components/Error'
import Detail from './pages/Detail'
import Login from './pages/Login'
import Loading from './components/Loading'
import Footer from './components/Footer'
import Header from './components/Header'
import Reg from './pages/Reg'

import {mapState} from 'vuex';

export default {
  name: 'app',
  components: {
    Header,Footer,Loading,Follow,Home,Error,Detail,Login,Reg
  },
  watch:{
    $route:{
      handler({path}){
        // console.log(path)
        if(/home|follow|column/.test(path)){
          this.$store.dispatch('VIEW_NAV',true)
          this.$store.dispatch('VIEW_FOOT',true)
        }
        if(/user/.test(path)){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',true)
        }
        if(/login|reg|detail/.test(path)){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',false)
      }
      },
      immediate:true
    }
  },
  computed:mapState(['bFoot','bLoad'])
}
</script>

